@Component({})
export default class NavigationBar extends Vue {
	@Prop({
		type: String,
		default: '#ffffff'
	})
	background: string
	@Prop({
		type: String,
		default: 'rgba(0, 0, 0, 1)'
	})
	color: string
	@Prop({
		type: String,
		default: '导航栏'
	})
	titleText: string
	@Prop({
		type: String,
		default: ''
	})
	titleImg: string
	@Prop({
		type: String,
		default: ''
	})
	backIcon: string
	@Prop({
		type: String,
		default: ''
	})
	homeIcon: string
	@Prop({
		type: Number,
		default: 16
	})
	fontSize: number
	@Prop({
		type: Number,
		default: 19
	})
	iconHeight: number
	@Prop({
		type: Number,
		default: 58
	})
	iconWidth: number

	navHeight: number = 0
	status: number = 0
	containerStyle: string = ''
	textStyle: string = ''
	iconStyle: string = ''

	/**
	 * @LastEditors: chuyinlong
	 * @description: 通过获取系统信息计算导航栏高度
	 */

	setNavSize() {
		const sysinfo = uni.getSystemInfoSync()
		const statusHeight = sysinfo.statusBarHeight
		const isiOS = sysinfo.system.indexOf('iOS') > -1
		const navHeight = !isiOS ? 48 : 44

		this.$STORE.setStore.setNavigationBarHeight(statusHeight + navHeight)

		this.status = statusHeight
		this.navHeight = navHeight
	}

	/**
	 * @LastEditors: chuyinlong
	 * @description: 设置样式至组件
	 */

	setStyle() {
		const containerStyle = ['background:' + this.background].join(';')

		const textStyle = [
			'color:' + this.color,
			'font-size:' + this.fontSize + 'px'
		].join(';')

		const iconStyle = [
			'width: ' + this.iconWidth + 'px',
			'height: ' + this.iconHeight + 'px'
		].join(';')

		this.containerStyle = containerStyle
		this.textStyle = textStyle
		this.iconStyle = iconStyle
	}

	/**
	 * @LastEditors: chuyinlong
	 * @description: 返回事件
	 */

	back() {
		uni.navigateBack({
			delta: 1
		})
		this.$emit(
			'back',
			{
				back: 1
			},
			{}
		)
	}

	/**
	 * @LastEditors: chuyinlong
	 * @description: 跳转至首页按钮
	 */

	home() {
		this.$emit('home', {}, {})
	}

	mounted() {
		this.setNavSize()
		this.setStyle()
	}
}
